import { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { requestCard } from '../../../redux/actions/scanner'
import ScannerError from '../scanner-error/ScannerError'
import { ApplicationState } from '../../../types'
import { getIsCameraEnabled } from '../../../redux/selectors/ui'
import { setIsCameraEnabled } from '../../../redux/actions/ui'
import { decodeGuid } from '../../../helpers'
import { Html5Qrcode } from 'html5-qrcode'
import './ScannerQR.scss'

export default function ScannerQR() {
  const dispatch = useDispatch()
  const isCameraEnabled = useSelector<ApplicationState, boolean>(getIsCameraEnabled)
  const [error, setError] = useState('')

  if (error) console.error({ error }) // TODO remove
  useEffect(() => {
    const video: any = document.getElementById('video')
    if (video) {
      navigator.mediaDevices
        .getUserMedia({
          video: {
            facingMode: { exact: 'environment' },
          },
        })
        .then(function (stream: any) {
          if ('srcObject' in video) {
            video.srcObject = stream
            video.play()
          } else {
            video.src = window.URL.createObjectURL(stream)
          }
          video.onloadedmetadata = function (e) {
            video.play()
          }
        })
        .catch(function (err) {
          dispatch(setIsCameraEnabled(true))
          console.log('An error occurred: ' + err)
        })
    }
    // eslint-disable-next-line
  }, [])

  return (
    <>
      {/* <ScannerHeader scannerPage /> */}
      <div className="scanner-qr ">
        {!isCameraEnabled ? <video id="video"></video> : <ScannerError cameraDisabled />}
      </div>
    </>
  )
}

