import { handleActions } from 'redux-actions'
import { ScannerState } from '../../types'
import { SET_CARD, SET_SCAN_ERROR, setCardActionType, setScanErrorActionType } from '../actions/scanner'

const initialState: ScannerState = {
  card: null,
  scanError: null,
}

const reducers = {
  [SET_CARD]: (state: ScannerState, { payload: card }: setCardActionType): ScannerState => {
    return {
      ...state,
      card,
    }
  },
  [SET_SCAN_ERROR]: (state: ScannerState, { payload }: setScanErrorActionType): ScannerState => {
    console.log(payload, 'scanError')
    return {
      ...state,
      scanError: payload,
    }
  },
}

export const scanner = handleActions<ScannerState, any>(reducers, initialState)
