import React, { useState } from 'react'
import { useSelector } from 'react-redux'
import { getCard, getScanError } from '../../redux/selectors/scanner'
import { ApplicationState } from '../../types'
import ScannerHeader from '../../components/Scanner/scanner-header/ScannerHeader'
import { CardResponseParams } from '../../types/bot'
import arrowDown from '../../assets/arrow-down.svg'
import arrowUp from '../../assets/arrow-up.svg'
import './Scanner.scss'
import ScannerError from '../../components/Scanner/scanner-error/ScannerError'

export default function Scanner() {
  const cardInformation = useSelector<ApplicationState, CardResponseParams>(getCard)
  const cardError = useSelector<ApplicationState, string>(getScanError)
  const [isOpenHistory, setIsOpenHistory] = useState<boolean>(false)

  const toggleIsOpenHistory = () => {
    setIsOpenHistory(!isOpenHistory)
  }

  const convertString = (str) => {
    if (str) {
      const [year, month, day] = str?.split('-')
      return `${day}.${month}.${year}`
    }
  }

  return (
    <div className="scanner">
      {!cardError && <ScannerHeader scannerPage />}
      {cardError ? (
        <ScannerError invalidQr />
      ) : (
        <>
          <div>
            <p className="scanner__title">Карточка конфигурационной единицы</p>
          </div>
          <div className="scanner__information">
            {cardInformation?.title && (
              <div className="scanner__information--instance">
                <p className="scanner__information--instance__name">Наименование</p>
                <p className="scanner__information--instance__value">{cardInformation.title}</p>
                <hr />
              </div>
            )}
            {cardInformation?.serialNumber && (
              <div className="scanner__information--instance">
                <p className="scanner__information--instance__name">Серийный номер</p>
                <p className="scanner__information--instance__value">{cardInformation.serialNumber}</p>
                <hr />
              </div>
            )}
            {cardInformation?.accountingNumber && (
              <div className="scanner__information--instance">
                <p className="scanner__information--instance__name">Бух.номер</p>
                <p className="scanner__information--instance__value">{cardInformation.accountingNumber}</p>
                <hr />
              </div>
            )}
            {cardInformation?.inventoryNumber && (
              <div className="scanner__information--instance">
                <p className="scanner__information--instance__name">Инвентарный номер</p>
                <p className="scanner__information--instance__value">{cardInformation.inventoryNumber}</p>
                <hr />
              </div>
            )}
            {cardInformation?.model && (
              <div className="scanner__information--instance">
                <p className="scanner__information--instance__name">Модель</p>
                <p className="scanner__information--instance__value">{cardInformation.model}</p>
                <hr />
              </div>
            )}
            {cardInformation?.status && (
              <div className="scanner__information--instance">
                <p className="scanner__information--instance__name">Статус</p>
                <p className="scanner__information--instance__value">{cardInformation.status}</p>
                <hr />
              </div>
            )}
          </div>
          <div className="scanner__divider" />
          <p className="scanner__title">Дополнительные реквизиты</p>
          <div className="scanner__information">
            {cardInformation?.location && (
              <div className="scanner__information--instance">
                <p className="scanner__information--instance__name">Место эксплуатации</p>
                <p className="scanner__information--instance__value m-0">{cardInformation.location}</p>
              </div>
            )}
            {cardInformation?.storage && (
              <div className="scanner__information--instance">
                <p className="scanner__information--instance__name">Место хранения</p>
                <p className="scanner__information--instance__value m-0">{cardInformation.storage}</p>
              </div>
            )}
            {cardInformation?.responsibleIt && (
              <div className="scanner__information--instance">
                <p className="scanner__information--instance__name">Ответственный сотрудник ИТ</p>
                <p className="scanner__information--instance__value m-0">{cardInformation.responsibleIt}</p>
              </div>
            )}
            {cardInformation?.operatedBy?.fullName && (
              <div className="scanner__information--instance mb-10">
                <p className="scanner__information--instance__name">Кем эксплуатируется</p>
                <p className="scanner__information--instance__value m-0">{`${
                  cardInformation?.operatedBy.fullName
                } c ${convertString(cardInformation?.operatedBy?.dateStart)}`}</p>
              </div>
            )}
          </div>
          {cardInformation?.operationHistory.length !== 0 && (
            <div onClick={() => toggleIsOpenHistory()} className="scanner__operation">
              <p className="scanner__operation--history">
                {isOpenHistory ? 'Cкрыть историю эксплуатации' : 'Показать историю эксплуатации'}
              </p>
              {isOpenHistory ? <img src={arrowDown} alt="arrow-down" /> : <img src={arrowUp} alt="arrow-up" />}
            </div>
          )}
          {isOpenHistory && (
            <div className="scanner__history">
              {cardInformation?.operationHistory?.map((instance) => (
                <p key={instance?.fullName} className="scanner__history--information">
                  {`${instance?.fullName} c ${convertString(instance?.dateStart)} по ${convertString(
                    instance?.dateEnd
                  )}`}
                </p>
              ))}
            </div>
          )}
        </>
      )}
    </div>
  )
}
