import React from 'react'
import qrInvalidGrey from '../../../assets/qr-invalid-grey.svg'
import qrInvalidGreySmall from '../../../assets/qr-invalid-grey-small.svg'
import qrInvalidRed from '../../../assets/qr-invalid-red.svg'
import ScannerHeader from '../scanner-header/ScannerHeader'
import './ScannerError.scss'

interface ScannerErrorProps {
  cameraDisabled?: boolean
  platformWeb?: boolean
  invalidQr?: boolean
}

function ScannerError({ cameraDisabled, platformWeb, invalidQr }: ScannerErrorProps) {
  return (
    <>
      {!invalidQr && !platformWeb ? <ScannerHeader cameraDisabled /> : <ScannerHeader scannerPage />}
      <div className={cameraDisabled ? 'scanner-error bg-dark' : 'scanner-error '}>
        {platformWeb && (
          <>
            <img src={qrInvalidGrey} alt="qr-invalid" className="scanner-error__qr" />
            <p className="scanner-error__invalid--platform m-0">
              Использование сканера возможно только на мобильных устройствах
            </p>
          </>
        )}
        {cameraDisabled && (
          <div className="scanner-error__camera-disabled">
            <p className="scanner-error__camera-disabled--title">Нет доступа к камере</p>
            <img src={qrInvalidGreySmall} alt="qr-invalid" className="scanner-error__qr" />
            <p className="scanner-error__camera-disabled--description m-0">
              Чтобы использовать «Сканер QR», необходимо предоставить доступ к камере приложению «eXpress» в настройках
              устройства
            </p>
          </div>
        )}
        {invalidQr && (
          <div className="scanner-error">
            <img src={qrInvalidRed} alt="qr-invalid" className="scanner-error__qr" />
            <p className="scanner-error__invalid--qr">Неверный QR-код. Попробуйте снова.</p>
          </div>
        )}
      </div>
    </>
  )
}

export default ScannerError
