import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { sendReady } from '../redux/actions/scanner'
import Scanner from '../containers/scanner/Scanner'
import { useQuery } from '@unlimited/smartapp-sdk'
import ScannerQR from '../components/Scanner/scanner-qr/ScannerQR'
import ScannerError from '../components/Scanner/scanner-error/ScannerError'
import MainLoader from '../components/ui/MainLoader/MainLoader'
import { getCard, getScanError } from '../redux/selectors/scanner'

export default function App() {
  const dispatch = useDispatch()
  const { platform } = useQuery()
  const error = useSelector(getScanError)
  const card = useSelector(getCard)
  const isScanResult = error || card
  const [load, setLoad] = useState(true)
  const [isPlatformWeb, setIsPlatformWeb] = useState<boolean>(false)
  useEffect(() => {
    if (load) {
      setLoad(false)
      dispatch(sendReady())
      // TODO setIsPlatformWeb(true) for prod
      if (platform === 'web') setIsPlatformWeb(false)
    }
  }, [dispatch, load, platform])

  return (
    <div>
      {isScanResult ? <Scanner /> : isPlatformWeb ? <ScannerError platformWeb /> : <ScannerQR />}
      <MainLoader />
    </div>
  )
}
