import { all, put, takeEvery } from 'redux-saga/effects'
import { Bridge as bridge, ready } from '@unlimited/smartapp-sdk'
import { GeneratorFunction } from '../../types'
import {
  EXIT_APP,
  REQUEST_CARD,
  requestCardActionType,
  RESET_STORE,
  SEND_READY,
  setCard,
  setScanError,
} from '../actions/scanner'
import { setIsCameraEnabled, setMainLoader } from '../actions/ui'
import { ERROR, METHODS } from '../../constants/constants'

export function* sendReadySaga(): GeneratorFunction {
  try {
    yield put(setMainLoader(true))
    yield ready()
  } catch (e) {
    console.error('sendReadySaga error: ', e)
  } finally {
    yield put(setMainLoader(false))
  }
}

export function* requestCardSaga({ payload }: requestCardActionType): GeneratorFunction {
  try {
    yield put(setMainLoader(true))
    const response = yield bridge?.sendBotEvent({
      method: METHODS.getCard,
      params: {
        guid: payload,
      },
    })

    yield response?.payload?.status === ERROR
      ? put(setScanError(response?.payload?.errors[0]?.reason))
      : put(setCard(response?.payload?.result))
  } catch (e) {
    console.error('requestCardSaga error', e)
  } finally {
    yield put(setMainLoader(false))
  }
}

export function* resetStoreSaga(): GeneratorFunction {
  try {
    yield put(setMainLoader(true))
    yield put(setCard(null))
    yield put(setScanError(null))
    yield put(setIsCameraEnabled(false))
  } catch (e) {
    console.error('resetStoreSaga error', e)
  } finally {
    yield put(setMainLoader(false))
  }
}

export function* exitAppSaga(): GeneratorFunction {
  try {
    yield put(setMainLoader(true))
    yield bridge?.sendClientEvent({
      method: 'open_smart_app',
      params: {
        appId: '',
      },
    })
  } catch (e) {
    console.error('exitAppSaga error', e)
  } finally {
    yield put(setMainLoader(false))
  }
}

export function* rootScannerSaga() {
  yield all([
    takeEvery(SEND_READY, sendReadySaga),
    takeEvery(REQUEST_CARD, requestCardSaga),
    takeEvery(RESET_STORE, resetStoreSaga),
    takeEvery(EXIT_APP, exitAppSaga),
  ])
}
