import { createAction } from 'redux-actions'
import { CardResponseParams } from '../../types/bot'

export const SEND_READY = 'SEND_READY'
export const REQUEST_CARD = 'REQUEST_CARD'
export const SET_CARD = 'SET_CARD'
export const SET_SCAN_ERROR = 'SET_SCAN_ERROR'
export const RESET_STORE = 'RESET_STORE'
export const EXIT_APP = 'EXIT_APP'

export const sendReady = createAction(SEND_READY)
export const requestCard = createAction(REQUEST_CARD, (guid: string) => guid)
export const setCard = createAction(SET_CARD, (card: CardResponseParams) => card)
export const setScanError = createAction(SET_SCAN_ERROR, (status: string) => status)
export const resetStore = createAction(RESET_STORE)
export const exitApp = createAction(EXIT_APP)

export type sendReadyActionType = ReturnType<typeof sendReady>
export type requestCardActionType = ReturnType<typeof requestCard>
export type setCardActionType = ReturnType<typeof setCard>
export type setScanErrorActionType = ReturnType<typeof setScanError>
export type resetStoreActionType = ReturnType<typeof resetStore>
export type exitAppActionType = ReturnType<typeof exitApp>
