import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { ReactComponent as ArrowBack } from '../../../assets/arrow-back.svg'
import { exitApp, resetStore } from '../../../redux/actions/scanner'
import { getIsCameraEnabled } from '../../../redux/selectors/ui'
import { ApplicationState } from '../../../types'
import './ScannerHeader.scss'

interface ScannerHeaderProps {
  cameraDisabled?: boolean
  scannerPage?: boolean
}

function ScannerHeader({ cameraDisabled, scannerPage }: ScannerHeaderProps) {
  const dispatch = useDispatch()
  const isCameraEnabled = useSelector<ApplicationState, boolean>(getIsCameraEnabled)
  console.log(isCameraEnabled)

  const onClickBack = () => {
    // isCameraEnabled || scannerPage ? dispatch(exitApp()) : dispatch(resetStore())
    dispatch(resetStore())
  }

  return (
    <div className={cameraDisabled ? 'scanner__header bg-dark' : 'scanner__header'}>
      {scannerPage && (
        <button
          onClick={onClickBack}
          className={cameraDisabled ? 'scanner__header--button bg-dark' : 'scanner__header--button'}
        >
          <ArrowBack
            className={cameraDisabled ? 'scanner__header--button__arrow-white' : 'scanner__header--button__arrow-blue'}
          />
        </button>
      )}
    </div>
  )
}

export default ScannerHeader
